@import 'styles/shared';

html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0;
  background: linear-gradient(
        rgba(255, 255, 255, 0) 0.9em,
        rgba(0, 0, 0, 0.15) 1em
      )
      0 0,
    linear-gradient(
        90deg,
        rgba(255, 255, 255, 0) 0.9em,
        rgba(0, 0, 0, 0.15) 1em
      )
      0 0;
  background-size: 1em 1em;
  background-color: ivory;
  // font: italic 16px/2 'Trebuchet MS', Bradley Hand, cursive;
  font: 16px/2 'Comic Sans MS', cursive, sans-serif;

  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
ul {
  list-style: none;
  padding: 0;
}

button {
  font-family: inherit;
}
textarea {
  resize: none;
}
img {
  max-width: 100%;
}
svg {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

input {
  padding-left: 10px;
  padding-bottom: 2px;
  caret-color: #e84a5f;
  border: none;
  border-bottom: 1px solid;
  background-color: inherit;
  outline: none;
}
input::placeholder {
  padding-left: 5px;
  color: #e84a5f;
  font-size: 0.8em;
  font-style: italic;
}
